import React, { createContext, useContext, useState, useEffect, useMemo, useCallback } from 'react';
import { useAuth } from './AuthContext';
import { products as initialProducts } from '../data/products';
import { defaultAdmins } from '../data/admins';

const AdminContext = createContext();

export const useAdmin = () => {
  const context = useContext(AdminContext);
  if (!context) {
    throw new Error('useAdmin must be used within an AdminProvider');
  }
  return context;
};

const normalizePhone = (phone) => {
  return phone.replace(/[\s()-]/g, '');
};

const defaultSettings = {
  logo: null,
  storeName: 'brandwatch',
  whatsapp: '+77778115151',
  telegram: '@baikadamov_a',
  paymentMethods: [
    { id: 1, name: 'Kaspi', enabled: true, details: '' },
    { id: 2, name: '', enabled: true, details: '' },
    { id: 3, name: '', enabled: true, details: '' }
  ],
  currency: '$'
};

export const AdminProvider = ({ children }) => {
  const { user, logout } = useAuth();
  // 
  const [admins, setAdmins] = useState(() => {
    const saved = localStorage.getItem('adminUsers');
    return saved ? JSON.parse(saved) : defaultAdmins;
  });

  //   
  const [currentAdmin, setCurrentAdmin] = useState(() => {
    const saved = localStorage.getItem('currentAdmin');
    return saved ? JSON.parse(saved) : null;
  });

  //  ( stock   )
  const [products, setProducts] = useState(() => {
    const saved = localStorage.getItem('adminProducts');
    const productsData = saved ? JSON.parse(saved) : initialProducts;
    //  stock = 5     
    return productsData.map(p => ({
      ...p,
      stock: p.stock !== undefined ? p.stock : 5,
      isArchived: p.isArchived || false
    }));
  });

  //  
  const [archivedProducts, setArchivedProducts] = useState(() => {
    const saved = localStorage.getItem('archivedProducts');
    return saved ? JSON.parse(saved) : [];
  });

  // 
  const [orders, setOrders] = useState(() => {
    const saved = localStorage.getItem('adminOrders');
    return saved ? JSON.parse(saved) : [];
  });

  //  
  const [settings, setSettings] = useState(() => {
    const saved = localStorage.getItem('adminSettings');
    return saved ? JSON.parse(saved) : defaultSettings;
  });

  //   localStorage
  useEffect(() => {
    localStorage.setItem('adminUsers', JSON.stringify(admins));
  }, [admins]);

  useEffect(() => {
    if (currentAdmin) {
      localStorage.setItem('currentAdmin', JSON.stringify(currentAdmin));
    } else {
      localStorage.removeItem('currentAdmin');
    }
  }, [currentAdmin]);

  useEffect(() => {
    localStorage.setItem('adminProducts', JSON.stringify(products));
  }, [products]);

  useEffect(() => {
    localStorage.setItem('archivedProducts', JSON.stringify(archivedProducts));
  }, [archivedProducts]);

  useEffect(() => {
    localStorage.setItem('adminOrders', JSON.stringify(orders));
  }, [orders]);

  useEffect(() => {
    localStorage.setItem('adminSettings', JSON.stringify(settings));
  }, [settings]);

  //   AuthContext
  useEffect(() => {
    if (user && user.role === 'admin') {
      // ,       (  )
      const normalizedPhone = normalizePhone(user.phone);
      const isAdminUser = admins.some(a => normalizePhone(a.phone) === normalizedPhone);
      
      if (isAdminUser) {
        setCurrentAdmin(user);
      } else {
        setCurrentAdmin(null);
      }
    } else {
      setCurrentAdmin(null);
    }
  }, [user, admins]);

  // ==========  ==========
  
  // ==========  ==========
  
  // ,    
  const isAdmin = useCallback((phone) => {
    const normalizedPhone = normalizePhone(phone);
    return admins.some(admin => normalizePhone(admin.phone) === normalizedPhone);
  }, [admins]);

  //  
  const adminLogin = useCallback((phone, password) => {
    const normalizedPhone = normalizePhone(phone);
    const admin = admins.find(a => 
      normalizePhone(a.phone) === normalizedPhone && a.password === password
    );
    if (admin) {
      setCurrentAdmin(admin);
      return { success: true, admin };
    }
    return { success: false, error: '   ' };
  }, [admins]);

  //  
  const adminLogout = useCallback(() => {
    logout();
    setCurrentAdmin(null);
  }, [logout]);

  //  
  const addAdmin = useCallback((adminData) => {
    const normalizedPhone = normalizePhone(adminData.phone);
    //   
    if (admins.some(a => normalizePhone(a.phone) === normalizedPhone)) {
      return { success: false, error: '     ' };
    }
    const newAdmin = {
      ...adminData,
      phone: normalizedPhone,
      id: Date.now()
    };
    setAdmins(prev => [...prev, newAdmin]);
    return { success: true, admin: newAdmin };
  }, [admins]);

  //   
  const updateAdmin = useCallback((id, updates) => {
    if (updates.phone) {
      updates.phone = normalizePhone(updates.phone);
      //    
      const exists = admins.some(a => a.id !== id && normalizePhone(a.phone) === updates.phone);
      if (exists) {
        return { success: false, error: '     ' };
      }
    }
    setAdmins(prev => prev.map(a => a.id === id ? { ...a, ...updates } : a));
    //      
    if (currentAdmin && currentAdmin.id === id) {
      setCurrentAdmin(prev => ({ ...prev, ...updates }));
    }
    return { success: true };
  }, [admins, currentAdmin]);

  //  
  const deleteAdmin = useCallback((id) => {
    //    
    if (admins.length <= 1) {
      return { success: false, error: '   ' };
    }
    //   
    if (currentAdmin && currentAdmin.id === id) {
      return { success: false, error: '   ' };
    }
    setAdmins(prev => prev.filter(a => a.id !== id));
    return { success: true };
  }, [admins, currentAdmin]);

  // ==========  () ==========
  
  //  
  const addProduct = useCallback((product) => {
    const newProduct = {
      ...product,
      id: Date.now(),
      createdAt: new Date().toISOString()
    };
    setProducts(prev => [...prev, newProduct]);
    return newProduct;
  }, []);

  //  
  const updateProduct = useCallback((id, updates) => {
    setProducts(prev => prev.map(p => 
      p.id === id ? { ...p, ...updates, updatedAt: new Date().toISOString() } : p
    ));
  }, []);

  //  
  const deleteProduct = useCallback((id) => {
    setProducts(prev => prev.filter(p => p.id !== id));
  }, []);

  //   
  const updateProductStock = useCallback((id, newStock) => {
    if (newStock <= 0) {
      //  0, 
      setProducts(prev => {
        const product = prev.find(p => p.id === id);
        if (product) {
          setArchivedProducts(archived => [{ ...product, stock: 0, isArchived: true, archivedAt: new Date().toISOString() }, ...archived]);
          return prev.filter(p => p.id !== id);
        }
        return prev;
      });
    } else {
      setProducts(prev => prev.map(p => 
        p.id === id ? { ...p, stock: newStock } : p
      ));
    }
  }, []);

  //    
  const restoreFromArchive = useCallback((id, newStock = 5) => {
    setArchivedProducts(prev => {
      const product = prev.find(p => p.id === id);
      if (product) {
        const restoredProduct = { 
          ...product, 
          stock: newStock, 
          isArchived: false,
          restoredAt: new Date().toISOString()
        };
        delete restoredProduct.archivedAt;
        setProducts(products => [restoredProduct, ...products]);
        return prev.filter(p => p.id !== id);
      }
      return prev;
    });
  }, []);

  //    
  const deleteFromArchive = useCallback((id) => {
    setArchivedProducts(prev => prev.filter(p => p.id !== id));
  }, []);

  //  
  const setProductDiscount = useCallback((id, discount) => {
    setProducts(prev => prev.map(p => {
      if (p.id === id) {
        const oldPrice = p.oldPrice || p.price;
        const newPrice = Math.round(oldPrice * (1 - discount / 100));
        return { 
          ...p, 
          discount, 
          oldPrice: discount > 0 ? oldPrice : null,
          price: discount > 0 ? newPrice : oldPrice
        };
      }
      return p;
    }));
  }, []);

  // ==========  ==========
  
  //      
  const addOrder = useCallback((orderData) => {
    const newOrder = {
      ...orderData,
      id: Date.now(),
      status: 'pending',
      createdAt: new Date().toISOString()
    };
    setOrders(prev => [newOrder, ...prev]);

    //     
    const itemsToArchive = [];
    
    setProducts(prev => {
      const updated = prev.map(product => {
        const orderedItem = orderData.items?.find(item => item.id === product.id);
        if (orderedItem) {
          const newStock = Math.max(0, (product.stock || 0) - (orderedItem.quantity || 1));
          if (newStock === 0) {
            itemsToArchive.push({ ...product, stock: 0, isArchived: true, archivedAt: new Date().toISOString() });
          }
          return { ...product, stock: newStock };
        }
        return product;
      });
      
      //       
      return updated.filter(p => p.stock > 0);
    });

    //   
    if (itemsToArchive.length > 0) {
      setArchivedProducts(prev => [...itemsToArchive, ...prev]);
    }

    return newOrder;
  }, []);

  //   
  const updateOrderStatus = useCallback((id, status) => {
    setOrders(prev => prev.map(o => 
      o.id === id ? { ...o, status, updatedAt: new Date().toISOString() } : o
    ));
  }, []);

  //  
  const deleteOrder = useCallback((id) => {
    setOrders(prev => prev.filter(o => o.id !== id));
  }, []);

  // ==========  ==========
  
  //  
  const updateSettings = useCallback((updates) => {
    setSettings(prev => ({ ...prev, ...updates }));
  }, []);

  //   
  const updatePaymentMethod = useCallback((id, updates) => {
    setSettings(prev => ({
      ...prev,
      paymentMethods: prev.paymentMethods.map(pm =>
        pm.id === id ? { ...pm, ...updates } : pm
      )
    }));
  }, []);

  //   
  const addPaymentMethod = useCallback((method) => {
    setSettings(prev => ({
      ...prev,
      paymentMethods: [...prev.paymentMethods, { ...method, id: Date.now() }]
    }));
  }, []);

  //   
  const deletePaymentMethod = useCallback((id) => {
    setSettings(prev => ({
      ...prev,
      paymentMethods: prev.paymentMethods.filter(pm => pm.id !== id)
    }));
  }, []);

  // ==========  ==========
  
  const getStatistics = useCallback(() => {
    const totalProducts = products.length;
    const totalOrders = orders.length;
    
    //     
    const completedStatuses = ['completed', 'delivered'];
    const totalRevenue = orders
      .filter(o => completedStatuses.includes(o.status))
      .reduce((sum, o) => sum + (o.total || 0), 0);
    
    //  
    const pendingStatuses = ['new', 'pending', 'processing'];
    const pendingOrders = orders.filter(o => pendingStatuses.includes(o.status)).length;
    
    //  
    const completedOrders = orders.filter(o => completedStatuses.includes(o.status)).length;
    
    //    7 
    const last7Days = [];
    for (let i = 6; i >= 0; i--) {
      const date = new Date();
      date.setDate(date.getDate() - i);
      const dateStr = date.toISOString().split('T')[0];
      const dayOrders = orders.filter(o => 
        o.createdAt && o.createdAt.startsWith(dateStr)
      );
      last7Days.push({
        date: dateStr,
        orders: dayOrders.length,
        revenue: dayOrders
          .filter(o => completedStatuses.includes(o.status))
          .reduce((sum, o) => sum + (o.total || 0), 0)
      });
    }

    //   (  ,   )
    const productSales = {};
    orders.forEach(order => {
      (order.items || []).forEach(item => {
        const itemId = item.id || item.title;
        if (!productSales[itemId]) {
          productSales[itemId] = { 
            id: item.id,
            brand: item.brand,
            title: item.title,
            image: item.image,
            totalSold: 0, 
            totalRevenue: 0 
          };
        }
        productSales[itemId].totalSold += item.quantity || 1;
        productSales[itemId].totalRevenue += (item.price || 0) * (item.quantity || 1);
      });
    });
    const topProducts = Object.values(productSales)
      .sort((a, b) => b.totalSold - a.totalSold)
      .slice(0, 5);

    return {
      totalProducts,
      totalOrders,
      totalRevenue,
      pendingOrders,
      completedOrders,
      archivedCount: archivedProducts.length,
      last7Days,
      topProducts
    };
  }, [products, orders, archivedProducts]);

  const value = useMemo(() => ({
    // 
    admins,
    currentAdmin,
    isAdmin,
    adminLogin,
    adminLogout,
    addAdmin,
    updateAdmin,
    deleteAdmin,
    // 
    products,
    archivedProducts,
    orders,
    settings,
    addProduct,
    updateProduct,
    deleteProduct,
    updateProductStock,
    restoreFromArchive,
    deleteFromArchive,
    setProductDiscount,
    addOrder,
    updateOrderStatus,
    deleteOrder,
    updateSettings,
    updatePaymentMethod,
    addPaymentMethod,
    deletePaymentMethod,
    getStatistics
  }), [admins, currentAdmin, products, archivedProducts, orders, settings, isAdmin, adminLogin, adminLogout, addAdmin, updateAdmin, deleteAdmin, addProduct, updateProduct, deleteProduct, updateProductStock, restoreFromArchive, deleteFromArchive, setProductDiscount, addOrder, updateOrderStatus, deleteOrder, updateSettings, updatePaymentMethod, addPaymentMethod, deletePaymentMethod, getStatistics]);

  return (
    <AdminContext.Provider value={value}>
      {children}
    </AdminContext.Provider>
  );
};

export default AdminContext;
