import React, { useState, useRef } from 'react';
import { useAdmin } from '../../context/AdminContext';
import { storage } from '../../lib/supabase';
import { checkRateLimit, recordUpload } from '../../utils/rateLimiter';
import './ImageUploader.css';

const ImageUploader = ({ images = [], onChange, productId = null, maxSizeMB = 20 }) => {
  const { currentAdmin } = useAdmin();
  const [uploading, setUploading] = useState(false);
  const [uploadProgress, setUploadProgress] = useState({});
  const fileInputRef = useRef(null);

  //   
  const allowedTypes = ['image/jpeg', 'image/jpg', 'image/png', 'image/webp'];
  const maxSizeBytes = maxSizeMB * 1024 * 1024;

  //  
  const validateFile = (file) => {
    //  
    if (!allowedTypes.includes(file.type)) {
      return {
        valid: false,
        error: ` . : JPG, JPEG, PNG, WEBP`
      };
    }

    //  
    if (file.size > maxSizeBytes) {
      return {
        valid: false,
        error: `  .  : ${maxSizeMB} `
      };
    }

    return { valid: true };
  };

  //   
  const uploadFile = async (file) => {
    //   
    if (!currentAdmin) {
      throw new Error('     ');
    }

    //  rate limit
    const rateLimitCheck = checkRateLimit();
    if (!rateLimitCheck.allowed) {
      const resetTime = new Date(rateLimitCheck.resetAt).toLocaleTimeString('ru-RU');
      throw new Error(`${rateLimitCheck.error}    ${resetTime}`);
    }

    //  
    const validation = validateFile(file);
    if (!validation.valid) {
      throw new Error(validation.error);
    }

    //  :      
    const fileTypeValid = await validateFileType(file);
    if (!fileTypeValid.valid) {
      throw new Error(fileTypeValid.error);
    }

    //  productId  ,   URL  
    if (!productId) {
      return new Promise((resolve) => {
        const reader = new FileReader();
        reader.onload = (e) => {
          resolve({
            url: e.target.result,
            isTemporary: true,
            file: file
          });
        };
        reader.readAsDataURL(file);
      });
    }

    //    rate limiting
    recordUpload();

    //   Supabase Storage
    //  productId    
    const productIdStr = productId ? String(productId) : `temp_${Date.now()}`;
    const fileExt = file.name.split('.').pop();
    const fileName = `${productIdStr}/${Date.now()}_${Math.random().toString(36).substring(7)}.${fileExt}`;
    
    const { url, error } = await storage.uploadProductImage(file, fileName, currentAdmin);
    
    if (error) {
      const errorMessage = error.message || '    ';
      throw new Error(` : ${errorMessage}`);
    }

    return {
      url,
      path: fileName,
      isTemporary: false
    };
  };

  //      (magic bytes)
  const validateFileType = (file) => {
    return new Promise((resolve) => {
      const reader = new FileReader();
      reader.onload = (e) => {
        const arrayBuffer = e.target.result;
        const bytes = new Uint8Array(arrayBuffer.slice(0, 12));
        
        //     
        const signatures = {
          jpeg: [[0xFF, 0xD8, 0xFF]],
          png: [[0x89, 0x50, 0x4E, 0x47, 0x0D, 0x0A, 0x1A, 0x0A]],
          webp: [[0x52, 0x49, 0x46, 0x46], [0x57, 0x45, 0x42, 0x50]] // RIFF...WEBP
        };

        let isValid = false;
        let detectedType = null;

        //  JPEG
        if (bytes[0] === signatures.jpeg[0][0] && 
            bytes[1] === signatures.jpeg[0][1] && 
            bytes[2] === signatures.jpeg[0][2]) {
          isValid = true;
          detectedType = 'jpeg';
        }
        //  PNG
        else if (signatures.png[0].every((byte, index) => bytes[index] === byte)) {
          isValid = true;
          detectedType = 'png';
        }
        //  WEBP
        else if (signatures.webp[0].every((byte, index) => bytes[index] === byte) &&
                 bytes[8] === signatures.webp[1][0] &&
                 bytes[9] === signatures.webp[1][1] &&
                 bytes[10] === signatures.webp[1][2] &&
                 bytes[11] === signatures.webp[1][3]) {
          isValid = true;
          detectedType = 'webp';
        }

        if (!isValid) {
          resolve({
            valid: false,
            error: '       '
          });
        } else {
          //   MIME-
          const expectedMime = {
            jpeg: ['image/jpeg', 'image/jpg'],
            png: ['image/png'],
            webp: ['image/webp']
          };

          if (!expectedMime[detectedType].includes(file.type)) {
            resolve({
              valid: false,
              error: 'MIME-     '
            });
          } else {
            resolve({ valid: true });
          }
        }
      };
      reader.onerror = () => {
        resolve({
          valid: false,
          error: '   '
        });
      };
      reader.readAsArrayBuffer(file.slice(0, 12));
    });
  };

  //   
  const handleFileSelect = async (e) => {
    const files = Array.from(e.target.files);
    if (files.length === 0) return;

    setUploading(true);
    const newImages = [...images];
    const progress = {};

    try {
      //     
      for (const file of files) {
        const validation = validateFile(file);
        if (!validation.valid) {
          alert(validation.error);
          setUploading(false);
          return;
        }
      }

      //  
      for (let i = 0; i < files.length; i++) {
        const file = files[i];
        progress[file.name] = 0;
        setUploadProgress({ ...progress });

        try {
          const result = await uploadFile(file);
          newImages.push(result);
          progress[file.name] = 100;
          setUploadProgress({ ...progress });
        } catch (error) {
          alert(`   ${file.name}: ${error.message}`);
        }
      }

      onChange(newImages);
    } catch (error) {
      alert(`: ${error.message}`);
    } finally {
      setUploading(false);
      setUploadProgress({});
      //  input
      if (fileInputRef.current) {
        fileInputRef.current.value = '';
      }
    }
  };

  //  
  const handleRemove = async (index) => {
    const imageToRemove = images[index];
    const newImages = images.filter((_, i) => i !== index);

    //     Supabase,  
    if (!imageToRemove.isTemporary && imageToRemove.path && productId) {
      try {
        await storage.deleteProductImage(imageToRemove.path);
      } catch (error) {
        console.error('     Storage:', error);
        //           Storage
      }
    }

    onChange(newImages);
  };

  //   (  )
  const handleMove = (index, direction) => {
    if (
      (direction === 'up' && index === 0) ||
      (direction === 'down' && index === images.length - 1)
    ) {
      return;
    }

    const newImages = [...images];
    const newIndex = direction === 'up' ? index - 1 : index + 1;
    [newImages[index], newImages[newIndex]] = [newImages[newIndex], newImages[index]];
    onChange(newImages);
  };

  return (
    <div className="image-uploader">
      <div className="image-uploader-header">
        <label className="image-uploader-label">
            *
          <span className="image-uploader-hint">
            (JPG, PNG, WEBP,  {maxSizeMB} )
          </span>
        </label>
        <input
          ref={fileInputRef}
          type="file"
          multiple
          accept="image/jpeg,image/jpg,image/png,image/webp"
          onChange={handleFileSelect}
          disabled={uploading}
          className="image-uploader-input"
        />
        <button
          type="button"
          onClick={() => fileInputRef.current?.click()}
          disabled={uploading}
          className="image-uploader-btn"
        >
          {uploading ? '...' : '+  '}
        </button>
      </div>

      {uploading && Object.keys(uploadProgress).length > 0 && (
        <div className="image-uploader-progress">
          {Object.entries(uploadProgress).map(([fileName, progress]) => (
            <div key={fileName} className="progress-item">
              <span>{fileName}</span>
              <div className="progress-bar">
                <div 
                  className="progress-fill" 
                  style={{ width: `${progress}%` }}
                />
              </div>
            </div>
          ))}
        </div>
      )}

      {images.length > 0 && (
        <div className="image-uploader-preview">
          {images.map((image, index) => (
            <div key={index} className="image-preview-item">
              <div className="image-preview-wrapper">
                <img 
                  src={image.url} 
                  alt={`Preview ${index + 1}`}
                  className="image-preview-img"
                />
                {image.isTemporary && (
                  <span className="image-temporary-badge"></span>
                )}
                <div className="image-preview-overlay">
                  <button
                    type="button"
                    onClick={() => handleMove(index, 'up')}
                    disabled={index === 0}
                    className="image-preview-btn"
                    title=" "
                  >
                    
                  </button>
                  <button
                    type="button"
                    onClick={() => handleMove(index, 'down')}
                    disabled={index === images.length - 1}
                    className="image-preview-btn"
                    title=" "
                  >
                    
                  </button>
                  <button
                    type="button"
                    onClick={() => handleRemove(index)}
                    className="image-preview-btn image-preview-btn-danger"
                    title=""
                  >
                    
                  </button>
                </div>
              </div>
              <div className="image-preview-number">{index + 1}</div>
            </div>
          ))}
        </div>
      )}

      {images.length === 0 && !uploading && (
        <div className="image-uploader-empty">
          <p>  </p>
          <p className="image-uploader-empty-hint">
             " "    
          </p>
        </div>
      )}
    </div>
  );
};

export default ImageUploader;
